
/* Dropdown Menu */

.dropdown-menu {
    background-color: #242424;
}

.container-logo-exit {
    display: flex;
    justify-content: flex-end;
}

.menu-exit {
    width: 35px;
    padding: 10px;
}

.dropdown-menu-list {
    text-align: center;
    padding: 220px 0px 220px 0px;
} 

.dropdown-menu-link {
    font-size: 5rem;
    font-weight: bold;
    margin: 125px 0px 30px 0px;
    color: #efefef;
    text-decoration: none;
}

.dropdown-menu-link:hover {
    color: #2CEAA3;
    font-size: 5.4rem;
}

