.image-profile-container {
    position: relative;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 200ms;
}

.image-profile-container.is-visible {
    transform: translate(-27%, 6%);
}

.image-profile {
    width: 90vw;
}

.text-container {
    position: absolute;
    padding: 0% 10% 0% 10%;
    z-index: 1;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 200ms;
    min-width: 100vw;
}

.text-container.is-visible {
    transform: translate(0%, -5%);
}

.title-profile {
    font-size: 4rem;
    font-weight: 900;
    transform: translate(320px, -230px);
    min-width: 100vw;
}

.text-profile-first-paragraphs {
    margin: 0% 0% 0% 0%;
    font-size: 1rem;
    transform: translate(135%, 37%);
    width: 41%;
}

.text-profile-last-paragraphs {
    margin: 0% 0% 0% 0%;
    font-size: 1rem;
    transform: translate(5%, 95%);
    width: 91%;
}

.links-profile-container {
    display: flex;
    flex-direction: column;
    margin-left: 25%;
    padding-top: 0%;
}

.link-profile {
    font-weight: 700;
    font-size: 1.3rem;
    text-decoration: none;
    padding-bottom: 3px;
}

.link-profile:hover {
    text-decoration: line-through;
}



