/* Dropdown Menu */.dropdown-menu {
  background-color: #242424;
}

.container-logo-exit {
  display: flex;
  justify-content: flex-end;
}

.menu-exit {
  width: 35px;
  padding: 10px;
}

.dropdown-menu-list {
  text-align: center;
  padding: 220px 0px 220px 0px;
}

.dropdown-menu-link {
  font-size: 5rem;
  font-weight: bold;
  margin: 125px 0px 30px 0px;
  color: #efefef;
  text-decoration: none;
}

.dropdown-menu-link:hover {
  color: #2CEAA3;
  font-size: 5.4rem;
}/* footer */.wrapper {
  min-height: 58vh;
  display: flex;
  flex-direction: column;
}

.page-body {
  flex-grow: 1;
}

.footer {
  flex-shrink: 0;
  border-top: 1px solid grey;
  flex-shrink: 0;
  margin: 0px 20px 0px 20px;
  padding: 40px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.footer.is-visible {
  opacity: 1;
}

.container-footer {
  display: flex;
  flex-direction: row;
  padding: 20px 0px 10px 0px;
}

.social-link {
  text-decoration: none;
  padding-bottom: 10px;
  font-size: 1.2rem;
  padding-right: 15px;
}

.social-link:hover {
  text-decoration: line-through;
}

.title-footer {
  font-size: 2.3rem;
  padding-bottom: 40px;
  font-style: italic;
  color: #c53a13;
}

.mail-footer {
  font-size: 1.3rem;
  text-decoration: none;
  padding-bottom: 10px;
  font-weight: 700;
}

.footer-credits-text {
  flex-shrink: 0;
}/* Media Queries for IPAD *//* Media Queries for Desktop *//* Main */.project-container {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 70px 30px 0px 30px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-container.is-visible {
  opacity: 1;
}/* Title Project */.project-title {
  font-size: 1.6rem;
  color: #c53a13;
  letter-spacing: 1px;
  font-weight: 800;
  padding-bottom: 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  text-transform: capitalize;
}

.project-title.is-visible {
  transform: translate(0px, -10px);
}

.project-subtitle {
  font-size: 2.6rem;
  padding-bottom: 20px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  text-transform: capitalize;
}

.project-subtitle.is-visible {
  transform: translate(0px, -10px);
}

.back-project-container {
  display: flex;
  align-items: center;
  margin-top: -23px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  z-index: 10;
}

.back-project.is-visible {
  transform: translate(0px, -10px);
}

.arrow-img {
  width: 15px;
  padding-right: 5px;
}/* Horizontal image */.cover-image {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.cover-image.is-visible {
  transform: translate(0px, -10px);
}/* Page section left */.project-page-section {
  position: relative;
  margin-top: 20px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-page-section.is-visible {
  opacity: 1;
}

.project-img-cover {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-cover.is-visible {
  transform: translate(0px, 45px);
  padding-bottom: 0px;
}

.text-container-section {
  position: absolute;
  width: 50vw;
  top: -3%;
  left: 50%;
}

.text-section-rigth {
  font-size: 0.9rem;
  font-weight: 500;
  padding: 15px 30px 15px 15px;
}

.text-section-full {
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
  padding: 15px 80px 15px 80px;
}/* Technologies section */.technologies-container {
  padding: 0px 30px 0px 30px;
  text-align: center;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.technologies-container.is-visible {
  opacity: 1;
}

.title-technologies {
  font-size: 2rem;
  padding-bottom: 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.title-technologies.is-visible {
  transform: translate(0px, -10px);
}

.logos-technologies-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 300ms;
}

.logos-technologies-container.is-visible {
  transform: translate(0px, -10px);
}

.technologies-icon {
  width: 25px;
  padding: 5px;
}/* Section Right */.project-page-section-right {
  position: relative;
  margin-top: 20px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
  text-align: right;
}

.project-page-section-right.is-visible {
  opacity: 1;
}

.project-page-section-right {
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-page-section-right.is-visible {
  opacity: 1;
}

.project-img {
  width: 100vw;
  margin-top: -15px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-full {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-full.is-visible {
  transform: translate(0px, -10px);
}

.project-img.is-visible {
  transform: translate(0px, -10px);
}

.text-container-section-two {
  position: absolute;
  width: 50vw;
  top: 30%;
}

.text-section {
  font-size: 0.9rem;
  text-align: left;
  font-weight: 500;
  padding: 0px 80px 15px 25px;
}/* Buttons section */.buttons-container {
  display: flex;
  flex-direction: column;
  margin: 10px 0px 50px 0px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.buttons-container.is-visible {
  opacity: 1;
}

.project-button-left {
  font-size: 1.8rem;
  text-decoration: none;
  padding: 5px;
  color: #242424;
  font-weight: 700;
  margin: 0px 0px 0px 70px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-button-left:hover {
  text-decoration: line-through;
}

.project-button-left.is-visible {
  transform: translate(0px, -10px);
}

.project-button-right {
  font-size: 1.4rem;
  text-decoration: none;
  padding: 5px;
  color: #242424;
  font-weight: 700;
  margin: 0px 0px 20px 200px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-button-right:hover {
  text-decoration: line-through;
}

.project-button-right.is-visible {
  transform: translate(0px, -10px);
}

.project-button:hover {
  font-size: 2.3rem;
}/* Media Queries for IPAD *//* Media Queries for Desktop */* {
  margin: 0px;
  padding: 0px;
  font-size: 10px;
  font-family: 'Montserrat', sans-serif;
  color: #242424;
  font-weight: 300;
}

body {
  background-color: #f8f8f8;
}/* Nav */.menu {
  display: flex;
  position: fixed;
  width: 100vw;
  z-index: 3;
  background-color: #f8f8f8;
}

.menu-logo-container {
  flex-grow: 1;
  z-index: -2;
}

.menu-logo {
  width: 25px;
  height: 25px;
  padding: 15px;
}

.menu-icon {
  width: 30px;
  height: 30px;
  padding: 15px;
}

.mobile-container-list {
  background-color: #242424;
  padding-top: 220px;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0px;
  left: 0px;
  text-align: center;
  z-index: -1;
  animation: opacity 300ms ease-in-out normal;
}

.mobile-container-list > li {
  list-style: none;
  margin-bottom: 5px;
}

.mobile-element {
  color: white;
  text-decoration: none;
  font-size: 3.3rem;
}

.mobile-element:hover {
  text-decoration: line-through;
}

.menu-list {
  display: none;
  visibility: hidden;
  list-style: none;
  text-align: center;
}/* No mobile menu */.menu-element {
  text-decoration: none;
  font-size: 1.35rem;
  display: block;
  padding: 10px;
  text-decoration: none;
  color: #242424;
  margin: 0 10px;
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.menu-element,.menu-element:after,.menu-element:before {
  transition: all .5s;
}

.menu-element:after {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 1px;
  content: '.';
  color: transparent;
  background: #f8f8f8;
  visibility: none;
  opacity: 0;
  z-index: -1;
}

.menu-element:hover {
  font-size: 1.7rem;
  font-weight: 500;
}

nav ul li a:hover:after {
  opacity: 1;
  visibility: visible;
  height: 100%;
}/* cover */.cover {
  display: flex;
  flex-direction: row;
  height: 100vh;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
  padding-top: 25px;
}

.cover.is-visible {
  opacity: 1;
}

.cover-container-text {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px 30px 0px 30px;
  text-align: left;
}

.cover-title {
  font-size: 1.6rem;
  color: #c53a13;
  letter-spacing: 1px;
  font-weight: 800;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  padding-bottom: 10px;
}

.cover-title.is-visible {
  transform: translate(0px, -25px);
}

.cover-paragraph {
  font-size: 3.4rem;
  line-height: 41px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 250ms;
}

.cover-paragraph.is-visible {
  transform: translate(0px, -25px);
}

.cover-social {
  display: flex;
  margin-top: 50px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  margin-top: 30px;
  margin-bottom: 30px;
